body {
  height: 100vh;
  background-color: #1f2632;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  font-family: Manrope, sans-serif;
  font-size: 1rem;
  display: flex;
}

.container {
  box-sizing: border-box;
  width: 100%;
  max-width: 540px;
  background-color: #323a49;
  border-radius: .9rem;
  margin: auto;
  padding: 3rem 2.81rem;
  position: relative;
}

.container .advice__number {
  text-align: center;
  color: #52ffa8;
  text-transform: uppercase;
  letter-spacing: .2rem;
  padding-bottom: 1.87rem;
  font-size: .8rem;
}

.container .advice__text {
  text-align: center;
  color: #cee3e9;
  font-size: 1.75rem;
  font-weight: 800;
}

.container .image-separate {
  width: 100%;
  height: 20px;
  background-image: url("pattern-divider-desktop.cc02f3b6.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  margin-top: 2.81rem;
  margin-bottom: 1.56rem;
}

.container .button {
  width: 4rem;
  height: 4rem;
  cursor: pointer;
  background-color: #52ffa8;
  background-image: url("icon-dice.294ce7d8.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto;
  border: none;
  border-radius: 50%;
  position: absolute;
  bottom: -2rem;
  left: 50%;
  transform: translateX(-50%);
}

.container .button:hover {
  box-shadow: 1px -5px 49px -5px #52ffa8;
}

.lds-spinner {
  color: official;
  width: 80px;
  height: 80px;
  display: inline-block;
  position: relative;
}

.lds-spinner div {
  transform-origin: 40px 40px;
  animation: lds-spinner 1.2s linear infinite;
}

.lds-spinner div:after {
  content: " ";
  width: 6px;
  height: 18px;
  background: #fff;
  border-radius: 20%;
  display: block;
  position: absolute;
  top: 3px;
  left: 37px;
}

.lds-spinner div:nth-child(1) {
  animation-delay: -1.1s;
  transform: rotate(0deg);
}

.lds-spinner div:nth-child(2) {
  animation-delay: -1s;
  transform: rotate(30deg);
}

.lds-spinner div:nth-child(3) {
  animation-delay: -.9s;
  transform: rotate(60deg);
}

.lds-spinner div:nth-child(4) {
  animation-delay: -.8s;
  transform: rotate(90deg);
}

.lds-spinner div:nth-child(5) {
  animation-delay: -.7s;
  transform: rotate(120deg);
}

.lds-spinner div:nth-child(6) {
  animation-delay: -.6s;
  transform: rotate(150deg);
}

.lds-spinner div:nth-child(7) {
  animation-delay: -.5s;
  transform: rotate(180deg);
}

.lds-spinner div:nth-child(8) {
  animation-delay: -.4s;
  transform: rotate(210deg);
}

.lds-spinner div:nth-child(9) {
  animation-delay: -.3s;
  transform: rotate(240deg);
}

.lds-spinner div:nth-child(10) {
  animation-delay: -.2s;
  transform: rotate(270deg);
}

.lds-spinner div:nth-child(11) {
  animation-delay: -.1s;
  transform: rotate(300deg);
}

.lds-spinner div:nth-child(12) {
  animation-delay: 0s;
  transform: rotate(330deg);
}

@keyframes lds-spinner {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

@media (max-width: 375px) {
  html {
    font-size: .9rem;
  }

  .container {
    max-width: 345px;
    padding-left: 1.81rem;
    padding-right: 1.81rem;
  }

  .container .image-separate {
    height: 16px;
    background-image: url("pattern-divider-mobile.7fb4141f.svg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    margin-top: 1.81rem;
  }
}

